<template>
    <div id="AccountSettingPassword">
      <div class="card" ref="card">
        <span class="title">修改密码</span>
        <hr>
        <vs-input primary placeholder="请输入原密码" v-model='form.oldPassword' type="password" :visiblePassword="hasVisiblePassword1">
          <template v-if="!form.oldPassword" #message-danger> 必填项 </template>
        </vs-input>
        <vs-input primary placeholder="请输入新密码" v-model='form.password' type="password" :visiblePassword="hasVisiblePassword2" class="mt-3">
          <template v-if="!form.password" #message-danger> 必填项 </template>
        </vs-input>
        <vs-input warn placeholder="确认新密码" v-model='form.confirmPassword' type="password" :visiblePassword="hasVisiblePassword3" class="my-3">
          <template v-if="(form.password !== form.confirmPassword) | !form.confirmPassword" #message-danger> 两次密码不一致 </template>
        </vs-input>
        <!-- <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px"  >
          <form @submit.prevent="" class="ChangeForm">
            <el-form-item label="原密码" prop="oldPassword">
                <el-input class="input" v-model.lazy="ruleForm.oldPassword" show-password></el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="password">
                <el-input class="input" v-model.lazy="ruleForm.password" show-password></el-input>
            </el-form-item>
            <el-form-item label="确认新密码" prop="confirmPassword">
                <el-input class="input" v-model.lazy="ruleForm.confirmPassword" show-password></el-input>
            </el-form-item>

            <hr>
            <button class="btn btn-success" @click="submitForm('ruleForm')" :disabled="doBtnDisabled">
              <div class="spinner-border mini text-light me-2" role="status" v-show="doBtnDisabled"></div>修改密码</button>
          </form>
        </el-form> -->

        <!-- <button class="btn btn-success" @click="ChangeStart" :disabled="doBtnDisabled">
          <div class="spinner-border mini text-light me-2" role="status" v-show="doBtnDisabled"></div>修改密码
        </button> -->
        <vs-button success size="large" @click="ChangeStart">修改密码</vs-button>
      </div>
      <div class="card mt-3 mb-3">
        <span class="title">管理令牌</span>
        <hr>
        <span class="text-danger font-weight-bolder text-center">暂未开放</span>
      </div>
      
    </div>
  </template>
  
  <script>
  import { modification_password } from '@/scripts/auth';
  export default {
    name: 'Password',
    data() {

      return {
        hasVisiblePassword1: false,
        hasVisiblePassword2: false,
        hasVisiblePassword3: false,
        form: {
          oldPassword: '',
          password: '',
          confirmPassword: '',
        },
        loading: null,
      }
    },
    methods: {
      ChangeStart() {
        if (!this.form.oldPassword || !this.form.password || !this.form.confirmPassword) {
          this.$toastx({
            type: 'warning',
            title: '请填写完整信息',
          })
          return
        }
        if (this.form.password !== this.form.confirmPassword) {
          this.$toastx({
            type: 'warning',
            title: '两次密码不一致',
          })
          return
        }
        this.$confirm('确认修改密码？', '提示', {
          confirmButtonText: '确认',
          cancelButtonText: '取消',
          type: 'warning'
        })
        .then (() => {
          this.loading = this.$vs.loading({
            target: this.$refs.card,
            text: '正在修改...',
          })
          modification_password({
            old_pwd: this.form.oldPassword,
            new_pwd: this.form.password
          })
          .then(() => {
            this.loading.close()
            this.loading = null
          })
        })
      }
    }
  }
  </script>
  
